.ui-border-t {
  position: relative; }

.ui-border-t:before {
    border-top: 1px solid #ddd;
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    right: auto;
    width: 100%;
    display: block;
    z-index: 15;
    -webkit-transform-origin: 100% 0;
            transform-origin: 100% 0;
    transform: scaleY(0.5);
    -webkit-transform: scaleY(0.5); }

.ui-container {
  height: 100%;
  width: 100%;
  position: absolute;
  background: #f5f5f9;
  left: 0;
  top: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column; }

.ui-container .amimate-item {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #000;
    overflow: hidden; }

.ui-container .amimate-item .am-content {
      background: #fff;
      -webkit-transform-origin: right;
              transform-origin: right;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      width: 100%;
      height: 100%; }

.rotateCubeInRight-enter-active,
.rotateCubeInRight-leave-active {
  -webkit-transition: -webkit-transform 500ms 0ms;
  transition: -webkit-transform 500ms 0ms;
  transition: transform 500ms 0ms;
  transition: transform 500ms 0ms, -webkit-transform 500ms 0ms; }

.rotateCubeInRight-enter-active .am-content {
  -webkit-transition: -webkit-transform 500ms 0ms;
  transition: -webkit-transform 500ms 0ms;
  transition: transform 500ms 0ms;
  transition: transform 500ms 0ms, -webkit-transform 500ms 0ms; }

.rotateCubeInRight-enter-active {
  -webkit-perspective: 2000px;
          perspective: 2000px;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%); }

.rotateCubeInRight-enter-active .am-content {
    -webkit-transform: rotateY(-90deg);
            transform: rotateY(-90deg); }

.rotateCubeInRight-leave-active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%); }

.rotateCubeInRight-enter-to {
  -webkit-transform: translateX(0%);
          transform: translateX(0%); }

.rotateCubeInRight-enter-to .am-content {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg); }

.rotateCubeInRight-leave-to {
  -webkit-transform: translateX(100%);
          transform: translateX(100%); }

.rotateCubeInLeft-enter-active,
.rotateCubeInLeft-leave-active {
  -webkit-transition: -webkit-transform 500ms 0ms;
  transition: -webkit-transform 500ms 0ms;
  transition: transform 500ms 0ms;
  transition: transform 500ms 0ms, -webkit-transform 500ms 0ms; }

.rotateCubeInLeft-leave-active .am-content {
  -webkit-transition: -webkit-transform 500ms 0ms;
  transition: -webkit-transform 500ms 0ms;
  transition: transform 500ms 0ms;
  transition: transform 500ms 0ms, -webkit-transform 500ms 0ms; }

.rotateCubeInLeft-enter-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%); }

.rotateCubeInLeft-enter-to {
  -webkit-transform: translateX(0%);
          transform: translateX(0%); }

.rotateCubeInLeft-leave-active {
  -webkit-perspective: 2000px;
          perspective: 2000px; }

.rotateCubeInLeft-leave-active .am-content {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg); }

.rotateCubeInLeft-leave-to {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%); }

.rotateCubeInLeft-leave-to .am-content {
    -webkit-transform: rotateY(-90deg);
            transform: rotateY(-90deg); }

